@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,500;1,300;1,900&display=swap);
.body {
    max-width: 800px;
    margin: 0 auto;
}
.search {
    width: 100%;
}

.search__input {
    width: 100%;
    margin-right: 20px;
}

.search-movie {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

.search__button {
    color: darkblue;
    font-family: Roboto;
    font-style: italic;
    font-weight: bold;
    font-size: 15px;
}
.cards {
    margin: 0;
    padding: 0;
    margin-top: 20px;
    list-style: none;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 15px;
    gap: 15px;
}


.card__image {
    margin: 0;
}

.card__image-slide {
    width: 100%;
}

.card__caption {
    font-family: Roboto;
    font-style: italic;
    font-weight: bold;
    font-size: 15px;
}

.card__more {
    margin: 0;
    color: cadetblue;
    font-family: Roboto;
    font-style: italic;
    font-weight: bold;
    font-size: 15px;
}

.card__more {
    cursor: pointer;
}

.card__details {
    padding: 10px;
    overflow-y: scroll;
    background: antiquewhite;
    position: absolute;
    max-width: 236px;
    max-height: 200px;
    display: none;
}

.card__details::-webkit-scrollbar {
    width: 7px;
    background-color: antiquewhite;
}

.card__details::-webkit-scrollbar-thumb {
    background-color: #223c50;
}

.card__details_open {
    display: block;
}
.header__title {
    font-family: Roboto;
    font-style: italic;
}
.card__info {
    font-family: Roboto;
    font-style: italic;
    font-size: 15px;
    padding: 0;
    margin: 0;
}

.card__info_bold {
    font-weight: bold;
}


.loading {
    display: block;
    margin: 0 auto;
}


